div.loader-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: rgba(255, 255, 255, 0.5);
}

.loader-container .loader {
    --size: 5rem;
    display: block;
    width: var(--size);
    height: var(--size);
    border: 0 solid transparent;
    border-radius: 50%;
    position: relative;
}
.loader-container .loader::after, .loader-container .loader::before {
    content: '';
	border: 0.25rem solid  rgb(33, 150, 243);
	border-radius: 50%;
	width: inherit;
	height: inherit;
    position: absolute;
	top: 0;
	left: 0;
    -webkit-animation: loader-animation 1s linear infinite;
            animation: loader-animation 1s linear infinite;
    opacity: 0;
}
.loader-container .loader::after {
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}
.loader-container .loader::before {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}


@-webkit-keyframes loader-animation {
    0% {
		transform: scale(0);
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: scale(1);
		opacity: 0;
    }
}


@keyframes loader-animation {
    0% {
		transform: scale(0);
		opacity: 0;
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: scale(1);
		opacity: 0;
    }
}

.loader-container .loader-text {
    display: block;
    margin-top: 0.5rem;
    font-family: consolas;
    text-align: center;
    font-size: 1.2rem;
    font-weight: 700;
    letter-spacing: 2px;
    color: transparent;
    position: relative;
}
.loader-container .loader-text::before {
    content: attr(data-text);
    position: absolute;
    top: 0;
    overflow: hidden;
    color:rgb(33, 150, 243);
    white-space: nowrap;
    -webkit-animation: loader-text-animation 3s linear infinite;
            animation: loader-text-animation 3s linear infinite;
}
@-webkit-keyframes loader-text-animation {
    0% {
        width: 0%;
    }
    50% {
        width: 100%;
    }
    100% {
        width: 100%;
    }
}
@keyframes loader-text-animation {
    0% {
        width: 0%;
    }
    50% {
        width: 100%;
    }
    100% {
        width: 100%;
    }
}
.progress-circle {
    font-size: 20px;
    margin: 20px;
    position: relative; /* so that children can be absolutely positioned */
    padding: 0;
    width: 5em;
    height: 5em;
    background-color: #F2E9E1; 
    border-radius: 50%;
    line-height: 5em;
 }
 
 .progress-circle:after{
     border: none;
     position: absolute;
     top: 0.35em;
     left: 0.35em;
     text-align: center;
     display: block;
     border-radius: 50%;
     width: 4.3em;
     height: 4.3em;
     background-color: white;
     content: " ";
 }
 /* Text inside the control */
 .progress-circle span {
     position: absolute;
     line-height: 5em;
     width: 5em;
     text-align: center;
     display: block;
     color: #53777A;
     z-index: 2;
 }
 .left-half-clipper { 
    /* a round circle */
    border-radius: 50%;
    width: 5em;
    height: 5em;
    position: absolute; /* needed for clipping */
    clip: rect(0, 5em, 5em, 2.5em); /* clips the whole left half*/ 
 }
 /* when p>50, don't clip left half*/
 .progress-circle.over50 .left-half-clipper {
    clip: rect(auto,auto,auto,auto);
 }
 .value-bar {
    /*This is an overlayed square, that is made round with the border radius,
    then it is cut to display only the left half, then rotated clockwise
    to escape the outer clipping path.*/ 
    position: absolute; /*needed for clipping*/
    clip: rect(0, 2.5em, 5em, 0);
    width: 5em;
    height: 5em;
    border-radius: 50%;
    border: 0.45em solid #53777A; /*The border is 0.35 but making it larger removes visual artifacts */
    /*background-color: #4D642D;*/ /* for debug */
    box-sizing: border-box;
   
 }
 /* Progress bar filling the whole right half for values above 50% */
 .progress-circle.over50 .first50-bar {
    /*Progress bar for the first 50%, filling the whole right half*/
    position: absolute; /*needed for clipping*/
    clip: rect(0, 5em, 5em, 2.5em);
    background-color: #53777A;
    border-radius: 50%;
    width: 5em;
    height: 5em;
 }
 .progress-circle:not(.over50) .first50-bar{ display: none; }
 
 
